body {
    background: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    /*justify-content: center;*/
    -ms-flex-align: center;
        align-items: center;
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0;
}

body, input, button {
    font-family: 'Comfortaa', cursive;
}

/*Gradient color gathered from https://uigradients.com/*/

.FireWatch {
    opacity: 0;
    background: #cb2d3e;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #ef473a, #cb2d3e);
    /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(#ef473a), to(#cb2d3e));
    background: -webkit-linear-gradient(left, #ef473a, #cb2d3e);
    background: -o-linear-gradient(left, #ef473a, #cb2d3e);
    background: linear-gradient(to right, #ef473a, #cb2d3e);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    -webkit-animation: FadeIn 1s forwards;
            animation: FadeIn 1s forwards;
}

.Lust {
    opacity: 0;
    background: #56ab2f;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to top, #a8e063, #56ab2f);
    /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left bottom, left top, from(#a8e063), to(#56ab2f));
    background: -webkit-linear-gradient(bottom, #a8e063, #56ab2f);
    background: -o-linear-gradient(bottom, #a8e063, #56ab2f);
    background: linear-gradient(to top, #a8e063, #56ab2f);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    -webkit-animation: FadeIn 1s forwards;
            animation: FadeIn 1s forwards;
}

